import React from 'react';
import {List,ListItem,ListItemText, Popover} from '@material-ui/core';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import  Poper from '../popper/index';

function Menu(props){
    const [menuList,setMenu]=React.useState(props.menuList);
    const [active,setActive]=React.useState(null);
    const updateChildrenMenu=(menu,accordion)=>{
        if(menu.children&&menu.children.length!=0){
            menu.accordion=accordion;
            return menu.children.map((menu)=>updateChildrenMenu(menu,accordion))
        }else{
            return menu;
        }
    }
    const showMenIcon=(menu)=>{
        if(menu.id.length===3&&!props.open){
            return true
        }else if(props.open){
            return true
        }
        return false;
    }
    const updateMenu=(id,menu)=>{
        if(menu instanceof Array){
            menu.forEach((item)=>updateMenu(id,item));
        }else if(menu.children&&menu.children.length!=0){
            if(id.includes(menu.id)){
                menu.accordion=true;
            }else{
                menu.accordion=false
            }
            menu.children.forEach((item)=>updateMenu(id,item));
        }else{
            if(menu.id===id){
                setActive(menu.name);
            }
        }
    }
    React.useEffect(()=>{
        if(props.id){
            updateMenu(props.id,menuList);
        }
     },[props.id]);
    const generateMenu=(menu)=>{
        let newMenu=[];
        if(menu instanceof Array){
            return menu.map((initMenu)=>generateMenu(initMenu));
        }else if(menu.children&&menu.children.length!=0){
            return (
                <div key={`${menu.id}`}>
                     <Poper open={!props.open} placement="right-start" hover child={
                        <React.Fragment>
                                  {menu.children.map((menuChildren)=>{
                            return generateMenu(menuChildren);
                        })}
                        </React.Fragment>
                    }>
                <ListItem button selected={menu.id===active} onClick={(e)=>{
                    menu.accordion= !menu.accordion;
                    menu.active= !menu.active;
                    if(menu.accordion){
                        menu=updateChildrenMenu(menu,true);
                    }
                    setActive(menu.id);
                    setMenu(Array.from(menuList));
                    if(props.getPath){
                        props.getPath(menu);
                    }
                    e.preventDefault();
                }}>
                    {showMenIcon(menu)?
                    <ListItemIcon>
                            {Icon[menu.preIcon]}
                    </ListItemIcon>:null
                    }
                    <ListItemText primary={props.local[props.local.locale].main[menu.name]}></ListItemText>
                    {props.open?menu.accordion?<ExpandLess/>:<ExpandMore/>:<ExpandMore></ExpandMore>}
                </ListItem>
                </Poper>
                {menu.accordion&&props.open?
                    <List spacing={4}>
                        {menu.children.map((menuChildren)=>{
                            return generateMenu(menuChildren);
                        })}
                    </List>:null
                }
                </div>
            )
        }else {
            newMenu.push(menu)
            return (
                <ListItem  button key={`${menu.id}`} selected={menu.id===active}  onClick={(e)=>{
                    menu.active= !menu.active;
                    setActive(menu.id);
                    if(props.getPath){
                        props.getPath(menu);
                    }
                    e.preventDefault();
                }}>
                   {showMenIcon(menu)?
                    <ListItemIcon>
                            {Icon[menu.preIcon]}
                    </ListItemIcon>:null
                    }
                    <ListItemText primary={props.local[props.local.locale].main[menu.name]}></ListItemText>
                </ListItem>
            )
        }

    }
    return (
        <div>
            <List spacing={4}>
                {generateMenu(menuList)}
            </List>
        </div>
    )
}
const Icon= {
    dashboard:<DashboardIcon/>,
    Components:<AssignmentTurnedInIcon/>
}
export default Menu;