import React from 'react';
import {Button as MuiButton} from '@material-ui/core'; 

function Button(props){
    const {onClick, ...reset}=props;
    const click=()=>{
        if(onClick)
        onClick();
    }
    const submit=()=>{
        if(props.type==='submit'){
            onClick();
        }

    }
    React.useEffect(()=>{
        document.addEventListener("keydown",(e)=>{
            if(e.keyCode===13){
                submit()
            }
        })

    })
  

    return (
        <MuiButton style={{textTransform:"none"}} onClick={click}{ ...reset}></MuiButton>
    )
}
export default Button;