import React from 'react';
import {Grid} from '@material-ui/core'

function Header(props){
    return (
        <Grid container direction="row" justify={props.left&&props.right?'space-between':!props.right?'flex-start':'flex-end'}>
            {props.left?
            <Grid item>
                {props.left}
            </Grid>:null
            }
            { props.right?
            <Grid item >
                {props.right}
            </Grid>:null
            }
        </Grid>
    )
}
export default Header;