import {combineReducers,applyMiddleware,compose} from 'redux'
import createSagaMiddleware from 'redux-saga'
import {createStore} from 'redux';
import loginReducer from '../service/login/loginReducer';
import mainReducer from '../service/main/mainReducer';
import localeReducer from '../service/locale/localReducer';
const sagaMiddleware = createSagaMiddleware();
const composeEnhancers =
  typeof window === 'object' &&
  window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ ?   
    window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
      // Specify extension’s options like name, actionsBlacklist, actionsCreators, serialize...
    }) : compose;
    const enhancer = composeEnhancers(
        applyMiddleware(sagaMiddleware),
        // other store enhancers if any
      );

const reducers=combineReducers({
    login:loginReducer,
    main: mainReducer,
    locate:localeReducer
});
const store= process.env.NODE_ENV === "development"?createStore(reducers,enhancer):createStore(reducers)
// sagaMiddleware.run(loginSag)
export default store;

