import React from 'react';
import {withRouter} from 'react-router-dom';
import {Button,Grid} from '@material-ui/core'
import Chart from '../../component/chart/index'
import Popover from '../../component/popper/index'

function Dashboard(){
    const [options,setOptions]=React.useState(window.MockData.chart.test1);
    return (
        <div>
            <Grid container>
                <Grid item xs>
                        <Chart options={options}></Chart>
                </Grid>
                <Grid item xs>
                <Chart options={window.MockData.chart.test2}></Chart>
                </Grid>
            </Grid>
           <Button onClick={()=>{
               window.MockData.chart.test1.series[0].data[2]=100;
               let newOptions=Object.assign({},window.MockData.chart.test1)
               setOptions(newOptions);
           }}>change</Button>
           <Popover options={[
               "个人资料",
               "退出"
           ]}>
               <Button>111</Button>
           </Popover>
        </div>
    )
}
export default Dashboard;