import {makeStyles} from '@material-ui/styles';
const loginStyle=makeStyles(()=>({
    box:{
      position: 'absolute',
      width:500,
      height:500,
      top:'6%',
      right:'3%',
      borderRadius:5,
      boxShadow: '5px 5px 10px #1f1f1f',
      background:'white'
    },
    Container:{
      height:window.innerHeight
    },
    boxOne:{
      background:'url(https://wallpaperm.cmcm.com/d387296146b59d12d6545e64d062d2e6.jpg )',
      height: window.innerHeight
    },
    mobile_box:{
      width:'100%',
      height:'100%',
      display:'flex',
      alignItems:'center',
      justifyContent:'center',
    },
    mobile_content:{
      height:300,
      width:400,
      boxShadow: '1px 5px 10px #1f1f1f',
      display:'flex',
      flexDirection:'column',
      alignItems:'center',
      justifyContent:'center',
      background:'white',
      position:'relative',
      bottom:25,
      borderRadius:5
    },
    index:{
      display:'flex',
      width:'100%',
      height:'100%',
      justifyContent:'center',
      alignItems:'center',
      flexDirection:'column'
    },
    content:{
      height:300,
      width:400,
      boxShadow: '1px 5px 10px #1f1f1f',
      display:'flex',
      flexDirection:'column',
      alignItems:'center',
      justifyContent:'center'
    }
}));
export default {
    loginStyle,
}