import React from 'react';
import {connect} from 'react-redux'
import * as login from '../../service/login/loginAction';
import {Container,Box,TextField} from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import style from '../../styles/login/login';
import { useTheme } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import {useForm} from 'react-hook-form';
import * as locale from '../../service/locale/action';
import { yupResolver } from '@hookform/resolvers/yup';
import Button from '../../component/button/index';
import * as yup from 'yup';


const  valid=yup.object().shape({
    user:yup.string().required(),
    password: yup.string().required()
})

function Login(props){
    const matches = useMediaQuery(useTheme().breakpoints.up('sm'));
    const classes=style.loginStyle();
    const {register,getValues,errors,trigger} = useForm({
        defaultValues:{
            user:'user',
            password:'password'
        },
        resolver:yupResolver(valid),
        mode:'onBlur'
    });
    return (
      <Box className={classes.boxOne}>
           <Container className={classes.Container}>
               {!matches?
                <Box className={classes.mobile_box}>
                <div className={classes.mobile_content}>
                <div>
                        <TextField 
                            label={props.locate[props.locate.locale].login.userName} 
                            inputRef={register}
                            name="user"
                            helperText={errors.user?errors.user.message:null}
                            error={errors.user}
                            style={{width:'90%'}}
                            variant="outlined"></TextField>
                  </div>
                  <div style={{height:30}}></div>
                  <div>
                        <TextField 
                        label={props.locate[props.locate.locale].login.password} 
                        inputRef={register}
                        helperText={errors.password?errors.password.message:null}
                        error={errors.password}
                        name="password"
                        type="password"
                        style={{width:'90%'}} 
                        variant="outlined"></TextField>
                  </div>
                  <div style={{height:30}}></div>
                  <div>
                  <Button color="primary" variant="contained" onClick={()=>{
                       const result= getValues();
                       if(result.user==="user" && result.password==="password"){
                            props.history.push("/main/dashboard");
                       }
                    }}>{props.locate[props.locate.locale].login.btn}</Button>
                  </div>
                </div>
           </Box>:
           <Box className={classes.box}>
           <div className={classes.index}>
               <div style={{height:50,width:400,textAlign:'center',fontSize:30}}>
                   Test System
               </div>
               <div style={{height:30,width:400}}></div>
               <div className={classes.content}>
                  <div>
                        <TextField 
                            label={props.locate[props.locate.locale].login.userName} 
                            inputRef={register}
                            name="user"
                            helperText={errors.user?errors.user.message:null}
                            error={errors.user}
                            style={{width:'90%'}}
                            variant="outlined"></TextField>
                  </div>
                  <div style={{height:30}}></div>
                  <div>
                        <TextField 
                        label={props.locate[props.locate.locale].login.password} 
                        inputRef={register}
                        error={errors.password}
                        name="password"
                        helperText={errors.password?errors.password.message:null}
                        type="password"
                        style={{width:'90%'}} 
                        variant="outlined"></TextField>
                  </div>
               </div>
               <div style={{height:30,width:400}}></div>
               <div style={{height:50,width:400,textAlign:'center'}}>
                   <Button type="submit" color="primary" variant="contained" onClick={()=>{
                       const result= getValues();
                      // trigger(()=>{
                        if(result.user==="user" && result.password==="password"){
                            props.history.push("/main");
                       }
                      // })
                      
                    }}>{props.locate[props.locate.locale].login.btn}</Button>
               </div>
           </div>
       </Box>
            }
      </Container>
      </Box>
     
    )
}
export default connect((state)=>({...state}),{...login,...locale})(withRouter(Login));