import React,{useContext} from 'react';
import mainStyle from '../../styles/index'
import CssBaseline from '@material-ui/core/CssBaseline';
import clsx from 'clsx';
import { IconButton,Select,MenuItem} from '@material-ui/core';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Divider from '@material-ui/core/Divider';
import {useTheme} from '@material-ui/core/styles';
import {Route,withRouter,Switch} from 'react-router-dom';
import Menu from '../../component/menu/index';
import router from '../../router';
import {connect} from 'react-redux';
import * as localActions from '../../service/locale/action';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Header from './header';
import Popover from '../../component/popper/index';
function Main(props){
    const classes=mainStyle.mainStyle();
    const [open,setOpen] = React.useState(true);
    const [id,setId]= React.useState(null);
    const handleDrawerOpen = () => {
        setOpen(true);
      };
    
      const handleDrawerClose = () => {
        setOpen(!open);
      };
      const List=[
        {label:props.locate[props.locate.locale].main.account,value:0},
        {label:props.locate[props.locate.locale].main.out,value:1}
      ];
    return (
        <div className={classes.root}>
            <CssBaseline></CssBaseline>
            <AppBar
              position='fixed'
              className={
                  clsx(classes.appBar,{
                      [classes.appBarShift]:open
                  })
              }
            >
            <Toolbar>
                <IconButton
                  color='inherit'
                  onClick={()=>handleDrawerOpen()}
                  style={{marginLeft:67}}
                  edge="start"
                  className={
                      clsx(
                          classes.menuButton,{
                              [classes.hide]:open
                          }
                      )
                  }
                >
                <MenuIcon/>
                </IconButton>
                <Header
                   right={
                       <>
                       <Select style={{color:"white"}}
                        className={classes.outlined}
                        variant="standard"
                        defaultValue="en"
                        classes={{
                            icon:classes.icon,
                        }}
                        onChange={
                           (e)=>{
                            props.changeLocation({locale:e.target.value });
                           }
                       }>
                           <MenuItem value="ch">中文</MenuItem>
                           <MenuItem value="en">English</MenuItem>
                       </Select>
                       <IconButton color="inherit">
                           <Popover hover options={List} open={true} selectItem={(item)=>{
                                if(item.value===1){
                                     props.history.push("/user/login");
                                }
                           }}>
                           <AccountCircle  fontSize="large"/>
                           </Popover>
                         
                       </IconButton>
                       </>
                   }
                />
            </Toolbar>
            </AppBar>
            <Drawer
              variant="permanent"
              className={
                  clsx(classes.drawer,{
                      [classes.drawerOpen]:open,
                      [classes.drawerClose]:!open
                  })
              }
              classes={
                  {
                      paper:clsx({
                          [classes.drawerOpen]:open,
                          [classes.drawerClose]:!open
                      })
                  }
              }
            >
                <div className={classes.toolbar}>
                    <IconButton onClick={()=>handleDrawerClose()}>
                        {open ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                    </IconButton>
               </div>
              <Divider />
                <Menu
                  menuList={window.MockData.MenuList}
                  id={id}
                  open={open}
                  local={props.locate}
                  getPath={(path)=>{
                      if(path.path){
                        props.history.push(path.path);
                      }
                   
                  }}
                />
            </Drawer>
            <main className={classes.content}>
            <div className={classes.toolbar} />
               <Switch>
                  {router.pc.main.map((item,index)=>{
                      return (
                          <Route exact path={item.path}  key={`${index}`} component={item.component}/>
                      )
                  })}
                </Switch>
            </main>
        </div>
    )
}
export default connect((state)=>({...state}),{...localActions})(withRouter(Main));