import React from 'react';
import * as echarts from 'echarts'; 

function Chart(props){
    const [chartId]=React.useState(Math.random());
    const [instance,setInStance]=React.useState(null);
    React.useEffect(()=>{
        let instance=echarts.init(document.getElementById(chartId));
        setInStance(instance);
        if(props.options){
            instance.setOption(props.options);
        }
        instance.on("click",(params)=>{
            if(props.click){
              props.click(params);
            }
        })
    },[])
    React.useEffect(()=>{
        if(instance){
            console.log(props.options);
            instance.clear();
            instance.setOption(props.options);
        }
    },[props.options])
    return (
        <div id={chartId} style={{height:300,width:300}}></div>
    )
}
export default Chart;