import * as Locale from '../../locale/index';
import * as types from './actionType';

const initState={
    locale:'en',
    ...Locale.default()
};
export default function(state=initState,action){
    switch(action.type){
        case types.CHANGE_LOCATE:{
            state.locale=action.params.locale
            return {
                ...state
            }
        }
        default:
            return {
                 ...state
            }
    }
}