import React from 'react';
import MaUTable from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Checkbox from '@material-ui/core/Checkbox';
import { useTable, useSortBy, useRowSelect } from 'react-table'
import update from 'immutability-helper'

function Table({columns,data,rowDrap,selectRow,selectType}) {
  const newColumns=React.useMemo(()=>columns?[...columns]:[],[]);
  const [newData, setRecords] = React.useState(data);
  const [drapId,setDrapId] = React.useState(null);
  const [radio,setRadio] = React.useState(null);
  
  const moveRow=(dragIndex,hoverIndex)=>{
    const dragRecord = newData[dragIndex];
    setRecords(
      update(newData,{
        $splice:[
          [dragIndex,1],
          [hoverIndex,0,dragRecord]
        ]
      })
    );
  }
  const MemoData=React.useMemo(()=>{
    return newData
  },[newData]);
  const { getTableProps, headerGroups, rows, prepareRow, state: { selectedRowIds }, } = useTable({
    columns:newColumns,
    data:MemoData,
  },
  useSortBy,
  useRowSelect,
  hooks=>{
    if(selectType==='checkbox'){
      hooks.visibleColumns.push(columns => [
        {
          id: 'selection',
          Header: ({ getToggleAllRowsSelectedProps }) => (
            <div>
                  <SelectCheckBox {...getToggleAllRowsSelectedProps()} />
            </div>
          ),
          Cell: ({ row }) =>{
              return (
                  <SelectCheckBox {...row.getToggleRowSelectedProps()} />
              )
          },
          component:'div'
        },
        ...columns,
      ])
    }
   
  }
  );
  React.useEffect(()=>{
    setRecords(data);
  },[data])
 
  React.useEffect(()=>{
    if(selectType==='radio'&&selectRow){
      selectRow({radio});
    }else if(selectRow){
      //selectRow(selectedRowIds);
    }
  },[selectedRowIds,radio]);
  return (
    <MaUTable component="div" {...getTableProps()}>
      <TableHead component="div">
        {headerGroups.map(headerGroup => (
          <TableRow component="div" {...headerGroup.getHeaderGroupProps()}>   
           {selectType==='radio'?<TableCell component="div"></TableCell>:null}
            {headerGroup.headers.map(column => (
              <TableCell component="div" {...column.getHeaderProps()}>
                {column.render('Header')}
                {column.isSort?
                <div {...column.getSortByToggleProps()}>
                  待处理
                </div> :null
              }
              </TableCell>
            ))}
          </TableRow>
        ))}
      </TableHead>
      <TableBody component="div">
        {rows.map((row, i) =>
           prepareRow(row) || 
          (
            <TableRow component="div" {...row.getRowProps()}
             onDragStart={(e)=>{
              if(!rowDrap){
                e.preventDefault();
              }
              setDrapId(i);
            }}
            >
              {selectType==='radio'?
              <SelectRadio  checked={radio===i} onChange={e=>{
                if(e.currentTarget.checked){
                  setRadio(i)
                }else{
                  setRadio(null);
                }
              }}/>:null  
            }
         
              {row.cells.map(cell => {
                return (
                  <TableCell 
                  component="div"
                  onDragOver={(e)=>{
                    e.preventDefault()
                  }}
                  onDrop={()=>{
                    if(i!==drapId){
                      moveRow(drapId,i);
                    }
                  }}
                
                   {...cell.getCellProps()}>
                    {cell.render('Cell')}
                  </TableCell>
                )
              })}
            </TableRow>
          )
        )}
      </TableBody>
    </MaUTable>
  )
}
const SelectCheckBox=React.forwardRef(({indeterminate, ...rest }, ref)=>{
    const defaultRef = React.useRef();
    const resolvedRef = ref || defaultRef;
    React.useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate
    }, [resolvedRef, indeterminate]);

  return (
        <Checkbox color="primary" ref={resolvedRef} {...rest}></Checkbox>
  )
});
const SelectRadio=({checked,onChange})=>{
  return (
    <TableCell component="div">
    <Checkbox color="primary" checked={checked} onChange={(e)=>{
      return onChange(e)
    }}/>
    </TableCell>
  )
}
export default Table;